<template>
    <article id="login">
        <h1>Login</h1>
        <input type="text" id="userid" v-model="user.userid">
        <input type="password" id="password" v-model="user.password">
        <input type="submit" value="login" v-on:click="login()">
    </article>
</template>

<script>
export default {
    name: 'login',
    data () {
        return {
            user: {
                userid: '',
                password: ''
            }
        }
    },
    methods: {
        login () {
            this.$http.post('/users/login', {
                user: this.user
            }).then(
                (res) => {
                    alert(res.data.message)
                },
                (err) => {
                    console.log(err)
                    alert('로그인 실패')
                }
            ).catch(err => {
                alert(err)
            })
        }
    }
}
</script>

<style scoped>
    #login{
        width: 500px;
        margin: 200px auto;
        padding: 20px;
        border: 1px solid #cccccc;
        background-color: #ffffff;
    }

    h1{
        margin-bottom: 12px
    }

    input{
        display: block;
        margin-bottom: 12px;
    }
</style>
