<template>
    <header id="header" class="header">
        <div class="header_wrap">
            <h1 class="header_logo"><a href="/">ManSo</a></h1>

            <nav class="header_nav">
                <ul class="header_menu">
                    <router-link to="/" tag="li" exact><a href="/">메인페이지</a></router-link>
                    <router-link to="/login" tag="li" v-if="!authenticated"><a href="/login">로그인</a></router-link>
                    <router-link to="/login" v-if="authenticated" v-on:click.native="logout()" replace>로그아웃</router-link>
                </ul>
            </nav>
        </div>
    </header>
</template>

<script>
export default {
    name: 'main-header',
    data () {
        return {
            authenticated: false,
            mockAccount: {
                username: 'manso',
                password: 'manso1234'
            }
        }
    },
    mounted () {
        if (!this.authenticated) {
            this.$router.replace({ name: 'login' })
        }
    },
    methods: {
        setAuthenticated (status) {
            this.authenticated = status
        },
        logout () {
            this.authenticated = false
        }
    }
}
</script>

